<template>
  <div class="row">
    <div class="col-sm-6">

    </div>
    <div class="col-sm-6 text-right">
      <pager-option :_page="_pageLink" @loadData_Father="_loadData"></pager-option>
    </div>
  </div>
</template>

<script>
import PagerOption from '@/components/Filter/PagerOption.vue'
export default {
  components: { PagerOption },
  name: 'FreightViewFooter',
  props: { 
    _pageLink: {
      type: Object
    }
  },
  methods: {
    _loadData(url) {
      this.$emit('_loadData_Father', url)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


