<template>
  <div>
    <button class="btn btn-light btn-sm btn-pager" @click="pageOption(false)">上一页</button>
    <div class="page-num-wrapper d-inline text-input">
      {{ nowPage() }}/{{ totalPage() }}
    </div>
    <button class="btn btn-light btn-sm btn-pager" @click="pageOption(true)">下一页</button>
  </div>
</template>

<script>
import tool from '@/common/es/tool'
import view from '@/common/es/view'
import conn_util from '@/common/es/conn_util'

export default {
  name: 'PageOption',
  props: {
    _page: {
      type: Object
    }
  },
  methods: {
    totalPage() {
      return conn_util.getTotalPage(this._page.limit, this._page.count)
    },
    nowPage() {
      return conn_util.getNowPage(this._page.offsetNow, this._page.limit)
    },
    pageOption(f) {
      let url = ''
      if (f) {
        url = this._page.next
      } else {
        url = this._page.previous
      }
      this.$emit('loadData_Father', url)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

.btn-pager
  padding-left: $horizonXL
  padding-right: $horizonXL
  border-radius: 30px

.btn-pager:hover, .btn-pager:active
  color: $primary !important
  border-color: $primary !important
  background: white !important

.btn-pager:active, .btn-pager:focus 
  box-shadow: 0 0 0 white !important

.page-num-wrapper
  margin-top: 4px
  line-height: 1.5
</style>


