<template>

  <div>

    <div class="row of-visible">
      <div class="col-sm-6 pt-2">
        <h5 class="fw-none">
          全部
        </h5>
      </div>
      <div class="col-sm-6 pt-1 text-right of-visible">

        <sort-option :_list="freights"></sort-option>

        <search-option @openSearch_Father="openSearch"></search-option>

      </div>
    </div>

    <table class="table">
      <tr>
        <td width="5%" class="text-accent">#</td>
        <td width="10%">编号</td>
        <td width="20%">菜品描述</td>
        <td width="15%" >价格</td>
        <td width="10%" >单位</td>
        <td width="30%" >标签</td>
        
        <td width="10%"></td>
      </tr>
    </table>
  </div>

</template>

<script> 
import tool from '@/common/es/tool'
import SortOption from '@/components/Filter/SortOption.vue'
import SearchOption from '@/components/Filter/SearchOption.vue'
export default {
  components: { SortOption, SearchOption }, 
  name: 'FreightViewTop',
  props: {
    freights: {
      type: Array
    }
  },
  methods: {
    openSearch() {
      tool.showModal('freightSearchModal')
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


