<template>
  <table class="table m-0 table-top-line-none">

    <tr v-for="(v, index) in freights" :key="v.id" :id="`freight_tr_` + v.id">
      <td width="5%" class="text-accent">{{ index + 1 }}</td>
      <td width="10%">{{ v.num }}</td>
      <td width="20%">{{ v.named }}</td>
      <td width="15%" >$&nbsp;{{ v.price }}</td>
      <td width="10%">&nbsp;&nbsp;{{ unit(v.unit) }}</td>
      <td width="30%" >
        <div 
          v-for="t in v.tag" 
          :key="t.id" 
          class="tag tag-table tag-table-fix" 
          :style="{ 'background': t.color }"
        >
          {{ t.named }}
        </div>
      </td>
      
      <td width="10%">

          <i @click="option(v.id, 'trash')" class="float-right">
            <remove></remove>
          </i>

          <i @click="option(v.id, 'update')" class="float-right">
            <pencil></pencil>
          </i>
        
      </td>
    </tr>

  </table>
</template>

<script>
import $ from 'jquery'
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'

import Pencil from '@/components/Option/Pencil.vue'
import Remove from '@/components/Option/Remove.vue'

export default {
  name: 'FreightViewBody',
  components: { Pencil, Remove },
  props: {
    freights: {
      type: Array
    }
  },
  methods: {
    unit (u) {
      return view.ser_unit(u)
    },
    option(id, option) { this.$emit('option_Father' , id, option) },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


